
html {
  box-sizing: border-box;
}


*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: @breakpoint-mobile;
  margin: 0;
  padding: 0;
  font-family: @font-family;
  font-size: @fs-base;
  line-height: @lh-normal;
  font-weight: @fw-regular;
  color: @main-color;
  overflow-x: hidden;
}


img {
  max-width: 100%;
  height: auto;
}


a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
