.intro-headings-mixin() {
  margin: 0;
  font-family: @font-family;
  font-size: @fs-main-heading;
  font-weight: @fw-light;
  color: @secondary-color;
}

.nav-links-mixin() {
  font-family: @font-family;
  font-size: @fs-subtitle;
  font-weight: @fw-bold;
  line-height: @lh-large;
  color: @secondary-color;
  text-transform: uppercase;
}

.buttons-mixin() {
  font-family: @font-family;
  font-size: @fs-subtitle;
  font-weight: @fw-bold;
  line-height: @lh-small;
}

.lists-mixin() {
  margin: 0;
  padding: 0;
  list-style: none;
}

.contacts-links-mixin() {
  font-family: @font-family;
  font-size: @fs-main-heading;
  font-weight: @fw-light;
  color: @accent-color;
}

.paragraph-reset() {
  margin: 0;
  padding: 0;
}

.heading-reset() {
  margin: 0;
  padding: 0;
}

.mobile-paddings() {
  padding-right: 20px;
  padding-left: 20px;
}

.tablet-paddings() {
  padding-right: 20px;
  padding-left: 20px;
}

.desktop-paddings() {
  padding-right: 10px;
  padding-left: 10px;
}
